import React, { Component } from "react";
import "./signup.css";

import PropTypes from "prop-types";

import { reduxForm, Field } from "redux-form";

import { register } from "../../redux/actions/auth";

const validate = values => {
  const errors = {};
  if (!values.email) {
    errors.email = "Required";
  } else if (!/^[\w-_]+@\w+.\w{2,4}$/i.test(values.email)) {
    errors.email = "Invalid email address";
  }
  if (!values.password) {
    errors.password = "Required";
  } else if (values.password.length < 6) {
    errors.password = "Must be more than 6 and less than 20 characters";
  }
  if (!values.repeat) {
    errors.repeat = "Required";
  } else if (values.repeat !== values.password) {
    errors.repeat = 'Must match the "Password" field';
  }
  return errors;
};

const input = ({
  id,
  input,
  name,
  label,
  type,
  meta: { touched, error, active }
}) => (
  <div className="signup__field">
    <label htmlFor={id}>
      <b>{label}</b>
    </label>
    <input
      className="signup__input"
      {...input}
      type={type}
      name={name}
      placeholder={label}
    />
    {!active &&
      touched &&
      (error && <span className="signup__error w3-red">{error}</span>)}
  </div>
);

class Signup extends Component {
  static propTypes = {
    handleSubmit: PropTypes.func,
    submit: PropTypes.func
  };

  render() {
    const { handleSubmit } = this.props;
    return (
      <div
        className="w3-modal-content block-signin"
        style={{ maxWidth: "600px" }}
      >
        <form
          id="signup"
          className="signup login-content w3-container"
          onSubmit={handleSubmit(register)}
        >
          <div className="imgcontainer w3-center">
            <img
              src="data:image/svg+xml;utf8;base64,"
              alt="Avatar"
              className="avatar"
            />
          </div>

          <div className="container">
            <Field
              component={input}
              id="signup_email"
              label="Email"
              type="email"
              name="email"
            />

            <Field
              component={input}
              id="signup_pswd"
              label="Password"
              type="password"
              name="password"
            />

            <Field
              component={input}
              id="signup_rpswd"
              label="Repeat password"
              type="password"
              name="repeat"
            />

            <button type="submit">Register</button>
          </div>
        </form>
      </div>
    );
  }
}

export default reduxForm({
  form: "signup",
  validate
})(Signup);



