import React, { Component } from "react";
import "./signin.css";

import PropTypes from "prop-types";

import { reduxForm, Field } from "redux-form";

import { login } from "../../redux/actions/auth";

const validate = values => {
  const errors = {};
  if (!values.email) {
    errors.email = "Required";
  } else if (!/^[\w-_]+@\w+.\w{2,4}$/i.test(values.email)) {
    errors.email = "Invalid email address";
  }
  if (!values.password) {
    errors.password = "Required";
  } else if (values.password.length < 6 && values.password.length <= 20) {
    errors.password = "Must be more than 6 and less than 20 characters";
  }
  return errors;
};

const input = ({
  id,
  input,
  name,
  label,
  type,
  meta: { touched, error, active }
}) => (
  <div className="signin__field">
    <label htmlFor={id}>
      <b>{label}</b>
    </label>
    <input
      className="signin__input"
      {...input}
      type={type}
      name={name}
      placeholder={label}
    />
    {!active &&
      touched &&
      (error && <span className="signin__error w3-red">{error}</span>)}
  </div>
);

class Signin extends Component {
  static propTypes = {
    handleSubmit: PropTypes.func,
    submit: PropTypes.func
  };

  render() {
    const { handleSubmit } = this.props;
    return (
      <div
        className="w3-modal-content block-signin"
        style={{ maxWidth: "600px" }}
      >
        <form
          id="sign"
          className="signin login-content w3-container"
          onSubmit={handleSubmit(login)}
        >
          <div className="imgcontainer w3-center">
            <img
              src="data:image/png;base64,"
              alt="Avatar"
              className="avatar"
            />
          </div>

          <div className="container">
            <Field
              component={input}
              id="signin_email"
              type="email"
              name="email"
              label="Email"
            />

            <Field
              component={input}
              id="signin_email"
              type="password"
              name="password"
              label="Password"
            />

            <button type="submit">Login</button>
          </div>
        </form>
      </div>
    );
  }
}

export default reduxForm({
  form: "signin",
  validate
})(Signin);



